.header {
    grid-area: h;
    background: linear-gradient(#3A0064, darkslateblue);
    border-radius: 8px;
}
.header img{
    width: 60px;
    height: 60px;
    margin-top: 10px;
    margin-left: 20px;


}

.loginBlock{
    float: right;
    margin-top: 20px;
    margin-right: 20px;

}
.login{
    color: white;
    font-size: 20px;
}

.button {
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    background-color: #520099;
    padding: 6px 12px;
    border-radius: 8px;
    margin-bottom: 5px;
}

.button:hover {
    background-color: #7536b4;
    color: #fff;
    text-shadow: #fff 0 0 4px;
    box-shadow: #7536b4 0 0 15px;
}